/* Suppose all cards' initial size is 480*300 */
div.Card{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
}
div.Card>p.CardName{
    font-size: var(--font-size-small);
    color: var(--color-black-dark);
}
div.Card>p.CardCountry{
    font-size: var(--font-size-tiny);
}
div.Card:hover{
    cursor: pointer;
}
div.Card-Featured{
    transform: scale(0.8);
    transition: all 0.75s;
}
div.Card-Featured:hover{
    transform: scale(1);
}
div.Card-Featured>img{
    width: 6rem;
}
div.Card-Normal{
    margin: 0.5rem 0.25rem;
}
div.Card-Normal>img{
    width: 100%;
}
div.Card-Normal:hover>img{
    animation: HorinzontalShaking ease-in-out 0.5s;
}
div.Card-Denomination,
div.Card-Arbitrary{
    margin-left: auto;
    margin-right: auto;
}
div.Card-Denomination>img,
div.Card-Arbitrary>img{
    width: 100%;
}
div.Card-Denomination>p.CardValue,
div.Card-Arbitrary>p.CardValue{
    color: var(--color-black-dark);
    font-weight: 700;
    margin-top: 0.5rem;
}
